.guide-tips-page {
  & > .content {
    padding-top: 40px;
  }

  .guide-tips-hero {
    .inner-content {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
    }
    .page-title {
      text-align: left;
    }
  }

  .bank-steps {
    padding-left: 40px;
    padding-right: 40px;
  }

  .table-body {
    padding: 24px 24px 14px 24px;
    .table-row {
      height: 115px;
      padding: 0;
      overflow: hidden;
      display: flex;
    }
  }

  .checking-saving {
    .inner-content {
      margin-top: 40px;
    }
  }

  @media only screen and (max-width: 1336px) {
    .bank-steps {
      padding-left: 16px;
      padding-right: 16px;
    }

    .checking-saving {
      .inner-content {
        margin-top: 32px;
      }
    }
  }

  @media only screen and (max-width: 960px) {
    .guide-tips-hero {
      .page-title {
        width: 60%;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
      }

      .image-container {
        max-height: 400px;

        img {
          margin-top: -15%;
        }
      }
    }
    .table-body {
      .table-row {
        height: 140px;
      }
    }
  }

  @media only screen and (max-width: 768px) {
    .content {
      width: 100%;
      padding-top: 50px;
      &:not(.main) {
        padding-bottom: 20px;
      }
    }
    .guide-tips-hero {
      .image-container {
        max-height: 300px;
      }
    }
  }
  @media only screen and (max-width: 600px) {
    .table-body {
      .table-row {
        height: 230px;
      }
    }
  }
  @media only screen and (max-width: 550px) {
    .guide-tips-hero {
      .image-container {
        max-height: 250px;
      }
    }
    .table-body {
      .table-row {
        height: 250px;
      }
    }
  }

  @media only screen and (max-width: 419px) {
    .guide-tips-hero {
      .image-container {
        max-height: 180px;
      }
    }
  }
}
