.footer {
  // background-color: #201f1a;
  padding-bottom: 24px;
  color: #696863;

  .footer-block {
    display: inline-block;
    padding-top: 80px;
    padding-right: 50px;
    max-width: 260px;
  }

  address {
    a {
      padding: 8px 0px;
      & > svg {
        margin-right: 8px;
      }
      &:hover {
        color: #FF8838;
      }
    }
  }

  ol {
    margin: 0;
    padding: 0;
    list-style-type: none;
    & > li {
      margin: 0;
      padding: 8px 0px;
      &:hover {
        color: #FF8838
      }
    }
  }

  .footer-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    height: 32px;
    width: 32px;
    padding: 4px;
    margin-right: 12px;
    background-color: #757575;
    color: black;
    border-radius: 50%;
  }

  @media only screen and (max-width: 1920px) {
    // desktop
    padding-left: 190px;
    padding-right: 190px;
  }
  @media only screen and (max-width: 1610px) {
    padding-left: 100px;
    padding-right: 100px;
  }
  @media only screen and (max-width: 1440px) {
    // tablet
    padding-left: 48px;
    padding-right: 48px;
  }
  @media only screen and (max-width: 768px) {
    // phone
    padding-left: 16px;
    padding-right: 16px;

    .inner-content {
      justify-content: flex-start;
    }
  }

  .footer-socail a:hover{
    transform: scale(1.1);
  }

  .footer-socail a img:hover{
    transform: scale(1.1);
  } 
}
