.trend-card {
  border-radius: 8px;
  box-shadow: 0px 0px 75px #edf6fb;
  max-width: 80%;
  margin-left: auto;
  position: relative;

  &:after {
    content: "";
    height: 250px;
    width: 250px;
    background-color: #eff0f1;
    position: absolute;
    border-radius: 50%;
    left: -60px;
    top: -60px;
    z-index: -1;
  }

  .inner {
    padding: 24px;
    background-color: white;
    border-radius: 8px;
  }

  &-img {
    display: flex;
    border-radius: 8px;
    justify-content: center;
    background-color: #fffcf5;
    & > img {
      //   width: 80%;
    }
  }

  &-list {
    width: 70%;
    &-item {
      border-top: 1px solid $color-grey;
      margin: 0;
      padding: 8px 0px;
    }
  }

  @media only screen and (max-width: 1440px) {
    &:after {
      height: 220px;
      width: 220px;
    }
  }
  @media only screen and (max-width: 1200px) {
    &:after {
      height: 200px;
      width: 200px;
    }
  }
  @media only screen and (max-width: 978px) {
    &:after {
      height: 170px;
      width: 170px;
    }
  }
  @media only screen and (max-width: 600px) {
    margin-right: auto;
    &:after {
      height: 150px;
      width: 150px;
    }
  }
}
