.header-buttons {
  display: inline;
  flex-wrap: nowrap;
  // align-items: center;
  vertical-align: middle  ;

  button {
    margin-left: 32px;
    border: 1px solid #000000;
    box-sizing: border-box !important;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 18px;
    border-radius: 0;
  }

  button:not(.search-button) {
    height: 40px;
    width: 114px;
  }

  .select-language {
    margin-left: 1rem;
  }

  .select-spin {
    margin-bottom: -7px;
  }

  .MuiSelect-select.MuiSelect-select {
    padding: 0px 24px;
  }

  .MuiSelect-select:focus {
    background-color: white;
  }
}


