.loan-card {
  padding: 0px 24px;
  background-color: #fbfbfb;
  border-radius: 2px;
  border: 1px solid $color-grey;
  box-sizing: border-box;
  box-shadow: 0px 0px 5px rgba(32, 30, 25, 0.08);

  &-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 24px;
    border-bottom: 1px solid $color-grey;
    box-sizing: border-box;

    &-icon {
      margin-right: 48px;
      position: relative;
      text-align: center;
      &-bg {
        position: absolute;
        top: 0;
        left: 50%;
        width: 56px;
        height: 56px;
        background-color: $color-grey;
        border-radius: 50%;
      }
      & > svg {
        position: relative;
        width: 100%;
        min-width: 50px;
        height: auto;
        max-height: 80px;
      }
    }
  }

  &-content {
    padding: 40px 24px;
  }

  @media only screen and (max-width: 768px) {
    padding: 0 16px;

    &-header {
      &-icon {
        &-bg {
          height: 40px;
          width: 40px;
        }
      }
    }
    &-content {
      padding: 24px;
    }
  }

  @media only screen and (max-width: 500px) {
    &-header {
      &-icon {
        &-bg {
          height: 30px;
          width: 30px;
        }
      }
    }
  }
}
