body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Mulish";
  color: $color-default;
  max-width: 100%;
  overflow-x: hidden;
}

a {
  color: unset;
  text-decoration: unset;
}

ul {
  padding-block-start: 0px;
  padding-inline-start: 0px;
  padding-left: 1rem;
  margin: 0;
  & > li {
    margin-block-start: 0.875rem;
    margin-block-end: 0.875rem;
  }

  &.ul-none {
    padding-left: 0;
    list-style-type: none;
  }
}

html,
body {
  min-width: 375px;
}
