.side-menu {
  .MuiPaper-root.MuiDrawer-paper {
    width: 440px;

    .header-buttons {
      justify-content: space-between;
      margin-bottom: 24px;
      button {
        width: 180px;
        margin-left: 0;
      }
    }

    .search-box-container {
      margin-bottom: 20px;

      .search-box {
        display: flex;
        align-items: "center";
        box-shadow: none;
        background: #f3f3f1;
        width: 100%;
        box-sizing: border-box;
      }
    }

    .header-container {
      .MuiTab-textColorInherit {
        opacity: 1;
      }
    }

    .tabs-container {
      .header-tabs {
        .MuiTabs-indicator {
          display: none;
        }

        .MuiTabs-flexContainer {
          flex-direction: column;

          button {
            padding: 16px 0;
            width: 100%;

            &:hover {
              background: #fff;

              .header-page-title,
              .header-page-subtitle {
                text-decoration: underline;
              }
            }

            .MuiTab-wrapper {
              align-items: flex-start;

              .header-page-title {
                font-weight: 700;
              }
              .header-page-subtitle {
                font-weight: 400;
              }
            }
          }
        }
      }
    }

    #lang_tab #lang_tab_en:focus {
      background-color: #dcdcdb;
      font-style: normal;
      font-weight: 800;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
    }
    #lang_tab #lang_tab_lv:focus {
      background-color: #dcdcdb;
      font-style: normal;
      font-weight: 800;
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
    }

    .MuiDivider-root {
      width: 100%;
      margin: 16px 0;
    }

    .other-pages {
      .other-page {
        a {
          p {
            font-size: 16px;
            font-weight: 700;
          }
        }

        &:hover {
          text-decoration: underline;
        }
      }
    }

    @media only screen and (max-width: 500px) {
      width: 300px;

      .header-buttons {
        margin-bottom: 20px;

        button {
          width: 130px;
          font-size: 13px;
        }
      }
      .search-box-container {
        margin-bottom: 16px;
      }

      .tabs-container {
        margin-top: 40px;
        .header-tabs {
          .MuiTabs-flexContainer {
            button {
              padding: 12px 0;
              .header-page-title,
              .header-page-subtitle {
                font-size: 14px;
              }
            }
          }
        }
      }

      .other-pages {
        a {
          p {
            font-size: 14px;
          }
        }
      }
    }
  }
}
