.contact-us-page {
  overflow: hidden;

  .contact-us {
    padding: 80px 0;

    .loader {
      margin-right: 15px;
      color: #ffffff;
    }

    &-form-grid-item {
      margin-bottom: 24px;

      &:nth-child(1), &:nth-child(3) {
        padding-right: 39px;
      }

      &:nth-child(2), &:nth-child(4) {
        padding-left: 39px;
      }
    }

    .text-required {
      padding-left: 10px;
      position: relative;

      &::before {
        content: "*";
        color: #FF8838;
        position: absolute;
        left: 0;
        top: 0;
      }
    }

    .contact-us-social {
      margin: 80px 0 0;
      padding: 24px 44px;

      &-grid-wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
      }

      &-grid-item {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-right: 24px;

        &:hover {
          cursor: pointer;
          svg {
            transform: scale(1.1);
          }
        }
        span {
          margin-left: 6px;
        }

        svg:not(.linked-in-icon) {
          min-width: 20px;
          min-height: 20px;
        }

        .linked-in-icon-container {
          height: 20px;
          width: 20px;
          background: #1275b1;
          border-radius: 50%;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }

    .form-control {
      span {
        font-weight: 600;
      }
    }

    .submit-btn-container {
      margin-top: 30px;
      display: flex;
      justify-content: flex-end;

      &.mobile {
        display: none;
      }
    }
  }

  @media only screen and (max-width: 960px) {
    .contact-us {
      padding: 56px 0 80px;

      .submit-btn-container {
        margin-top: 0;
      }

      .contact-us-social {
        padding: 24px;
      }

      &-form-grid-item {
        margin-bottom: 32px;
  
        &:nth-child(1), &:nth-child(3) {
          padding-right: 8px;
        }
  
        &:nth-child(2), &:nth-child(4) {
          padding-left: 8px;
        }
      }

      .submit-btn-container {
        &.desktop {
          display: none;
        }
  
        &.mobile {
          display: flex;
          width: 100%;
        }
      }
    }
  }

  @media only screen and (max-width: 599px) {
    .contact-us {
      &-form-grid-item {
        margin-bottom: 32px;
  
        &:nth-child(1), &:nth-child(n + 1) {
          padding: 0;
        }
      }

      &-social {
        flex-direction: column;
        align-items: flex-start;
  
        &-grid-wrapper {
          justify-content: flex-start !important;
        }
  
        &-grid-item {
          justify-content: flex-start !important;
          min-width: 90px;
        }
  
        &-grid-row {
          margin-bottom: 16px;
        }
  
        h5 {
          margin-bottom: 32px;
        }
      }
    }
    
  }

  @media only screen and (max-width: 500px) {
    .contact-us {
      padding: 48px 0 80px;

      .contact-us-social {
        padding: 32px 24px;
      }
    }
  }
}
