.about-us {
  display: block;
  overflow: hidden;

  .about-us-hero {
    color: $color-white;
    background-color: $color-default;
    background-position: left bottom;
    background-size: 20%;
    background-repeat: no-repeat;
    background-image: url("../../assets/fi-bg.png");
    padding: 85px 0;
    margin-bottom: 120px;

    &-container-img {
      display: flex;
      justify-content: flex-end;
      align-items: center;

      img {
        width: 635px;
        height: 443px;
      }
    }

    &-text {
      .our-mission-text {
        margin-bottom: 16px;
      }

      .our-mission-content {
        line-height: 27px;
      }

      .title {
        margin-bottom: 40px;
        line-height: 44px;
      }
    }
  }

  .about-us-card {
    height: 100%;
    width: 252px;
    padding: 22px;
    box-sizing: border-box;
    border: 1px solid $color-grey;

    &-header {
      position: relative;
      & > i {
        display: inline-block;
        position: absolute;
        left: 50%;
        width: 56px;
        height: 56px;
        background-color: #ebebeb;
        border-radius: 50%;
        z-index: -1;
      }
      & > img {
        width: 90px;
        height: 90px;
      }
    }
  }

  .our-values {
    margin-bottom: 80px;
  }

  .our-values-content {
    line-height: 24px;
  }

  @media only screen and (max-width: 960px) {
    .about-us-hero {
      &-text {
        text-align: center;
        margin-bottom: 30px;

        .par1,
        .par2 {
          display: inline;
        }
      }
      &-container-img {
        justify-content: center;
      }
    }

    .about-us-card {
      width: 300px;

      &-wrapper {
        &:nth-child(1), &:nth-child(2) {
          margin-bottom: 24px;
        }
        &:nth-child(2), &:nth-child(4) {
          padding-left: 10px;
        }
        &:nth-child(1), &:nth-child(3) {
          display: flex;
          justify-content: flex-end;
          padding-right: 10px;
        }
        &:nth-child(2), &:nth-child(4) {
          padding-left: 10px;
        }
      }
    }
  }

  @media only screen and (max-width: 768px) {
    .about-us-hero {
      &-text {
        .title {
          margin-bottom: 14px;
          line-height: 33px;
        }
        .par1,
        .par2 {
          line-height: 24px;
        }
      }

      &-container-img {
        img {
          width: 481px;
          height: 335px;
        }
      }

      padding: 48px 0 24px;
      margin-bottom: 85px;
    }
    .about-us-values {
      margin-top: 5px;
    }
  }

  @media only screen and (max-width: 500px) {
    .about-us-hero {
      &-text {
        .our-mission-text {
          margin-bottom: 14px;
        }

        .title {
          margin-bottom: 24px;
        }
      }

      &-container-img {
        img {
          width: 345px;
          height: 240px;
        }
      }

      padding: 30px 0 55px;
      margin-bottom: 60px;
    }

    .about-us-card {
      &-wrapper {
        &:nth-child(1), &:nth-child(n + 1) {
          margin-bottom: 24px;
          padding: 0;
          display: flex;
          justify-content: center;
        }
        &:nth-child(4) {
          margin-bottom: 0;
        }
      }
    }
  }
}
