.container {
  width: 1336px;
  margin: auto;
  box-sizing: border-box;
}

.content {
  margin: auto;
  padding: 0px 0px 80px;
  width: 1262px;
  box-sizing: border-box;
}

.content-wrapper {
  margin: 0 auto;
  max-width: 1122px;
  box-sizing: border-box;
  padding: 0 30px;
}

.full-height {
  height: 100%;
}

@media only screen and (max-width: 1336px) {
  .content {
    width: 100%;
    max-width: 1262px;
    padding: 80px 48px;
  }

  .container {
    width: 100%;
    padding: 0px 16px;
  }
}

@media only screen and (max-width: 600px) {
  .content {
    width: 100%;
    padding: 80px 16px;
  }

  .container {
    width: 100%;
    padding: 0px 16px;
  }
}

