.typo {

  font-family: "Mulish" !important;
  font-style: normal;
  line-height: 56px;

  /* FONT WEIGHT */
  &.typo-weight {
    &-light {
      font-weight: 200;
    }
    &-normal {
      font-weight: 300;
    }
    &-medium {
      font-weight: 500;
    }
    &-semi-bold {
      font-weight: 600;
    }
    &-bold {
      font-weight: bold;
    }
    &-bolder {
      font-weight: 700;
    }
  }

  /* ALIGN */
  &.typo-align {
    &-right {
      text-align: right;
    }
    &-left {
      text-align: left;
    }
  }

  &-link {
    cursor: pointer;
    transition: all 0.2s linear;
    &:hover {
      opacity: 0.7;
    }
  }

  &-transform {
    &--cap {
      text-transform: capitalize;
    }
    &--init {
      text-transform: initial;
    }
    &--up {
      text-transform: uppercase;
    }
  }

  &.typo-link {
    text-decoration: underline;
  }

  &.italic {
    font-style: italic;
  }

  &.typo-content {
    margin-top: 1rem;
  }

  &.typo-mt-1 {
    margin-top: 1rem;
  }

  &.typo-mt-2 {
    margin-top: 2rem;
  }

  &.typo-mt-3 {
    margin-top: 3rem;
  }
  
  &.typo-mt-4 {
    margin-top: 4rem;
  }

  &.typo-partner-logo {
    max-width: 490px;
  }

  &.typo-partner-phone-logo {
    max-width: 490px;
    margin: 0 auto;
  }

  &.typo-clinic-card-content {
    font-weight: 400;
  }

  &.typo-clinic-card-phone-content {
    font-weight: 400;
    margin: 0 auto;
  }
    
}
