.home-page {
  .home-hero {
    background-color: #f9f8f5;

    .content {
      position: relative;
      background-size: 100% 100%;
      background-repeat: no-repeat;
      background-position: -850px -200px;
      overflow: hidden;

      .fi-icon {
        background-repeat: no-repeat;
        height: 70%;
        width: 26%;
        position: absolute;
        background-size: contain;
        right: -8%;
        bottom: -17%;
      }

      .worm-icon {
        left: 55%;
        top: 20px;
        width: 20px;
        height: 40px;
        position: absolute;
      }

      .three-lines-icon {
        left: 88%;
        top: 210px;
        width: 50px;
        height: 60px;
        position: absolute;
      }
    }

    .home-hero-img-container {
      z-index: 2;
      text-align: center;
      img {
        max-width: 100%;
      }
    }
  }

  .content.cards {
    padding-bottom: 0;
    section {
      max-width: 1262px;
      margin-left: auto;
      margin-right: auto;
    }

    .financial-insight-card {
      border-radius: 8px;
      box-shadow: 0px 0px 2px #d8d8d8;

      img.credit-cards {
        position: absolute;
        left: 43%;
        transform: translateX(-50%);
        z-index: 1;
      }

      p.credit-cards {
        position: absolute;
        bottom: 50px;
      }
      &-title {
        white-space: nowrap;
        text-align: center;
      }
    }
  }

  .circles-container {
    position: relative;

    &:after {
      content: "";
      background-size: 100% 100%;
      background-repeat: no-repeat;
      background-position: bottom right;
      position: absolute;
      right: 60px;
      bottom: -280px;
      width: 150px;
      height: 640px;
      z-index: -1;
    }
  }

  .home-signup-banner {
    background: #f9f8f5;
    position: relative;

    .image-container {
      display: flex;

      img {
        align-self: flex-start;
      }
    }

    &::after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      content: "";
      background-position: 150px 50px;
      background-size: 25%;
      background-repeat: no-repeat;
    }
  }

  .home-info {
    .content {
      padding-top: 0;
    }

    .home-info-banner {
      position: relative;
      .three-lines-icon {
        left: 69%;
        top: 77%;
        width: 10%;
        height: 10%;
        position: absolute;
      }

      .worm2-icon {
        left: 16%;
        top: 17%;
        width: 8%;
        height: 8%;
        position: absolute;
      }
    }

    .home-info-card {
      position: relative;
      padding: 24px 32px;
      background-color: #f9f8f5;
      border-radius: 8px;
      z-index: 1;
      & > img {
        width: 100%;
      }
      .home-info-card-icon {
        position: absolute;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 8px;
        padding: 16px;
        background-color: $color-white;
        box-shadow: 0px 0px 5px rgba(32, 30, 25, 0.08);
        cursor: pointer;

        & > svg {
          height: 32px;
          width: 32px;
        }

        &--top {
          top: -32px;
          left: 20%;
        }
        &--left {
          left: -32px;
          bottom: 20%;
        }
        &--right {
          right: -32px;
          top: 20%;
        }
      }
    }
  }

  .home-trends {
    background-position: left -69%;
    background-repeat: no-repeat;
    padding-top: 20px;
  }

  .home-footer {
    position: relative;

    .content {
      min-height: 228px;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    .background-container {
      position: absolute;
      width: 80%;
      height: 85%;
      right: 0;
      bottom: 0;
      background-size: contain;
      background-position: 100% 100%;
      background-repeat: no-repeat;
      background-image: url("../../assets/home-footer.png");
    }
  }

  @media only screen and (max-width: 1920px) {
    .content:not(.cards) {
      // padding: 10px 190px 80px 125px;
      width: 100%;
    }
  }
  @media only screen and (max-width: 1610px) {
    .content:not(.cards) {
      padding: 80px 100px 80px 100px;
    }
  }
  @media only screen and (max-width: 1440px) {
    .content:not(.cards) {
      padding: 80px 48px 80px 84px;
    }
    .circles-container {
      &:after {
        content: "";
        width: 130px;
        height: 500px;
        bottom: -200px;
      }
    }
  }

  @media only screen and (max-width: 1336px) {
    .content {
      max-width: none;
    }
  }

  @media only screen and (max-width: 1200px) {
    .home-info {
      .home-info-banner:after {
        width: 220px;
        height: 220px;
      }
    }

    .home-signup-banner {
      &::after {
        background-position: left bottom;
      }
    }
  }

  @media only screen and (max-width: 960px) {
    .home-hero {
      .content {
        .home-hero-text {
          .title {
            margin-left: auto;
            margin-right: auto;
          }
        }
        .fi-icon {
          height: 36%;
        }
        .worm-icon {
          top: 35%;
          left: 70%;
        }
        .three-lines-icon {
          left: 93%;
          top: 70%;
        }
      }
      &-text {
        text-align: center;
        p {
        }
      }

      .home-hero-img-container {
        img {
          margin-left: 0;
        }
      }
    }

    .circles-container {
      &:after {
        content: "";
        width: 100px;
        height: 400px;
        bottom: -100px;
      }
    }

    .home-info {
      .home-info-banner {
        &:after {
          width: 200px;
          height: 200px;
        }
      }
    }
  }

  @media only screen and (max-width: 768px) {
    .home-hero {
      .content {
        .fi-icon {
          height: 36%;
        }
      }
    }
    .content:not(.cards) {
      padding: 80px 16px;
    }

    .content.cards {
      .financial-insight-card {
        img.credit-cards {
          left: 50%;
        }
        p.credit-cards {
          bottom: 25px;
        }
      }
    }

    .home-info {
      .title {
        max-width: 70%;
        margin-top: 30px;
        text-align: center;
      }

      .home-info-banner:after {
        width: 150px;
        height: 150px;
      }
    }
  }

  @media only screen and (min-width: 600px) {
    .content {
      .content-inner > div:nth-child(2) {
        margin-left: 50%;
      }
    }

    .image-container {
      position: absolute;
      left: 15%;
      z-index: 2;
    }
  }

  @media only screen and (max-width: 600px) {
    .image-container {
      display: flex;
      justify-content: center;
    }

    .circles-container {
      &:after {
        content: none;
      }
    }
  }

  @media only screen and (max-width: 500px) {
    .home-info {
      .title {
        margin-top: 10px;
      }

      .home-info-banner:after {
        width: 100px;
        height: 100px;
        top: -38px;
        left: -38px;
      }
    }
  }
}
